html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  background: #FFF8F0;
  background-color: #e0dddc;
  background-image: linear-gradient(62deg, #e0dddc 0%, #f1e3c2 100%); }

.main {
  width: 100%;
  display: grid;
  max-width: 1000px;
  margin: 32px auto;
  grid-template-areas: "header header" "about content";
  grid-template-columns: 200px 1fr;
  text-align: center; }
  @media (max-width: 375px) {
    .main {
      grid-template-areas: "header" "content" "about";
      font-size: 20px; } }

.header {
  grid-area: header; }
  .header__picture {
    display: block;
    width: 100%; }

.about {
  grid-area: about; }

.content {
  grid-area: content; }

.articles {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 16px;
  grid-row-gap: 16px; }
  @media (max-width: 640px) {
    .articles {
      grid-template-columns: 1fr; } }

.article__Header {
  color: #92140C;
  font-size: 62px;
  margin-top: 30px;
  margin-bottom: 30px; }

.article__title {
  color: #111D4A;
  margin-top: 50px; }

.article__description {
  color: #1B1B1E;
  padding: 16px;
  margin-bottom: 30px; }

.article__image {
  margin: 30px auto; }

.article__link {
  color: #1556e4; }

.knowMore__header {
  color: #322F32; }

.navigation__list {
  list-style: none;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: center; }
  @media (max-width: 640px) {
    .navigation__list {
      flex-direction: column;
      align-items: flex-start;
      display: none; } }
  @media (max-width: 640px) {
    .navigation__list--visible {
      display: flex; } }

.navigation__item {
  display: block;
  margin: 0 16px; }
  @media (max-width: 640px) {
    .navigation__item {
      margin: 16px; } }

.navigation__link {
  color: #92140C; }

@media (max-width: 640px) {
  .navigation__switcher {
    border: none;
    background: none;
    position: absolute;
    right: 16px;
    top: 16px; } }

@media (min-width: 641px) {
  .navigation__switcher {
    display: none; } }

.site-header {
  margin: 32px; }
  @media (min-width: 641px) {
    .site-header {
      min-height: 48px; } }
